body {
  background-color: #f0f0f0;
}

.container {
  margin: 20px auto;
  width: 80%;
}

.dashboard-table {
  margin: 20px auto;
  width: 100%;
  padding: 0;
  table-layout: fixed;
  border: 1px solid #ccc;
  border-collapse: collapse;
}

.dashboard-table a {
  color: #353535;
  text-decoration: none;
}

.dashboard-table a:hover {
  color: #6b6b6b;
}

.dashboard-table tr {
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  padding: 0.35em;
}

.dashboard-table th,
.dashboard-table td {
  border: 1px solid #ddd;
  padding: 0.625em;
  text-align: center;
}

.Checked {
  background-color: #d42c26;
}

.Checking {
  background-color: #ffa4ab;
}

.In.Progress {
  background-color: #ffca28;
}

.ToDo {
  background-color: #cccccc;
}

.Done {
  background-color: #7bc36e;
}
